import React, { useState } from 'react'
import styled from 'styled-components'
import { motion, useTransform } from 'framer-motion'

const List = styled(motion.ul)`
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  list-style: none;
  margin: 0 ${props => props.marginLeft}px;
`

const ListItem = styled.li``

const Link = styled.a`
  text-decoration: none;
`

export default function MenuComponent(props) {
  const { progress } = props

  const marginLeftValue = useTransform(progress, [10, 90], [50, 150])
  const [marginLeft, setMarginLeft] = useState(marginLeftValue.get())
  marginLeftValue.onChange(setMarginLeft)

  return (
    <List marginLeft={marginLeft}>
      <ListItem>
        <Link href="/">Home</Link>
      </ListItem>
      <ListItem>
        <Link href="/carro-de-som">Carro de som</Link>
      </ListItem>
      <ListItem>
        <Link href="/radio-indoor">Rádio indoor</Link>
      </ListItem>
      <ListItem>
        <Link href="/studio-de-gravacao">Studio de gravação</Link>
      </ListItem>
    </List>
  )
}
